import { Controller } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export default function getTableIsSelected(table: Table, controller: Controller) {
  const { selection, document } = controller.value;
  if (
    selection.anchor.key === table.key
    || selection.focus.key === table.key
  ) {
    return false;
  }

  const { anchor, focus } = selection.convertToTextPoints(document);
  const isSelectionInsideTable =
    table.getNode(anchor.key) && table.getNode(focus.key);

  return Boolean(isSelectionInsideTable);
}
